@font-face {
  font-family: "Clear Sans";
}

html {
  
  overflow: hidden;
 
}

body {
  background: #bdc3c7;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #2c3e50, #bdc3c7);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #2c3e50, #bdc3c7);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position: relative;
  
  overflow: hidden;
}

#highscoreDiv {
  padding-top: 3em;
  font-weight: bold;
  color: white;
}

#boardDiv {
  box-sizing: content-box;
}

#aboutDiv {
  padding-top: 3em;
  font-weight: bold;
  color: white;
  text-align: center;
}

.score {
  color: white;
}

.scoreboard {
  color: white;
}

.text {
  color: white;
  padding-top: 40px;
  width: 440px;
  font-weight: bold;
}

.board {
  width: 440px;
  height: 440px;
  padding: 5px;
  box-sizing: content-box;
  background-color: #212121;
  border-radius: 7px;
  outline: none;
  position: relative;
}

.board .cell,
.tile {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
}

.cell,
.tile {
  width: 100px;
  height: 100px;
  margin: 5px;
  line-height: 90px;
  display: inline-block;
  font-size: 55px;
  font-weight: bold;
  text-align: center;
  vertical-align: middle;
  border-radius: 7px;
  font-family: "Clear Sans";
  color: #766;
  background-color: #fff;
}

.tile0 {
  background-color: #dcb;
}

.tile2 {
  background-color: #909090;
  color: white
}

.tile4 {
  color: white;
  background-color: #0f4c75;
}

.tile8 {
  color: #ffe;
  background-color: #3282b8;
}

.tile16 {
  color: #ffe;
  background-color: #bbe1fa;
}

.tile32 {
  color: #ffe;

  background-color: #6155a6;
}

.tile64 {
  color: #ffe;
  background-color: #a685e2;
}

.tile128 {
  color: #ffe;
  background-color: #ec7;
  font-size: 45px;
}

.tile256 {
  color: #ffe;
  background-color: #ec6;
  font-size: 45px;
}

.tile512 {
  color: #ffe;
  background-color: #ec5;
  font-size: 45px;
}

.tile1024 {
  color: #fff;
  background-color: #ec3;
  font-size: 35px;
}


.tile2048 {
  color: white;
  background: #a8ff78;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #78ffd6, #a8ff78);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #78ffd6, #a8ff78);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-size: 35px;
}

.tile4096 {
  color: #fff;
  background: #b92b27;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #1565C0, #b92b27);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #1565C0, #b92b27);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  font-size: 35px;
}

.tile8192 {
  color: #fff;
  background: #5433ff;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #5433ff, #20bdff, #a5fecb);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #5433ff, #20bdff, #a5fecb);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-size: 35px;
}

.tile {
  position: absolute;
}

.tile.merged {
  display: none;
}

.tile.merged.isMoving {
  display: inline;
}

.tile.new,
.overlay {
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
  -webkit-animation-name: newTile;
  animation-name: newTile;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-delay: 0.15s;
  animation-delay: 0.15s;
  -webkit-transform: scale(0);
  transform: scale(0);
}

@-webkit-keyframes newTile {
  from {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes newTile {
  from {
    -webkit-transform: scale(0);
    transform: scale(0);
  }

  to {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.overlay {
  position: absolute;
  top: 0px;
  bottom: 0px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  text-align: center;
  left: 0px;
  right: 0px;
  font-size: 55px;
  font-weight: bolder;
  background-color: rgba(221, 221, 221, 0.5);
  border-radius: 7px;
}

.tryAgain {
  background-color: #876;
  color: #fff;
  height: 40px;
  width: 200px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.overlay .message {
  color: #666;
}

